A,gBAAA;EACA,gBAAA;CACA;AACA;EACA,kBAAA;EACA,aAAA;CACA;AACA;EACA,sBAAA;EAAA,kCAAA;UAAA,0BAAA;CACA;AACA;EACA,qBAAA;EAAA,sBAAA;EAAA,cAAA;CACA;AACA;EACA,YAAA;EACA,YAAA;EACA,aAAA;EACA,mBAAA;EACA,uBAAA;CACA;AACA;EACA,uBAAA;EACA,iBAAA;EACA,YAAA;CACA;AACA;EACA,qBAAA;EAAA,sBAAA;EAAA,cAAA;EACA,sBAAA;EAAA,kCAAA;UAAA,0BAAA;CACA;AACA;EACA,kBAAA;CACA;AACA;EACA,qBAAA;EAAA,sBAAA;EAAA,cAAA;EACA,gBAAA;CACA;AACA;EACA,iBAAA;CACA;AACA;EACA,aAAA;CACA;AACA;EACA,mBAAA;CACA","file":"Comments.vue","sourcesContent":["<style>\r\n#comments {\r\n  margin-bottom: 50px;\r\n}\r\n.glyphicon {\r\n  cursor: pointer;\r\n}\r\n.commet {\r\n  border-bottom: 1px solid #eee;\r\n  padding: 10px 0;\r\n}\r\n.commet .content {\r\n  padding: 10px;\r\n  background: #eee;\r\n}\r\n.commet .commeter_info {\r\n  display: flex;\r\n}\r\n.commet .name_time {\r\n  font-size: 12px;\r\n  padding-top: 10px;\r\n  line-height: 20px;\r\n}\r\n.show_commonets .title {\r\n  border-bottom: 1px solid #eee;\r\n  padding-bottom: 5px;\r\n}\r\n.show_commonets .title small {\r\n  font-size: 12px;\r\n  cursor: pointer;\r\n}\r\n.show_commonets .title .right {\r\n  text-align: right;\r\n  float: right;\r\n}\r\n.ok_btn {\r\n  justify-content: flex-end;\r\n}\r\n.edit_place {\r\n  display: flex;\r\n}\r\n.image_url {\r\n  margin: 5px;\r\n  width: 50px;\r\n  height: 50px;\r\n  border-radius: 50%;\r\n  border: 1px solid #eee;\r\n}\r\n.commonet_edit {\r\n  border: 1px solid #eee;\r\n  background: #eee;\r\n  width: 100%;\r\n}\r\n.btn_place {\r\n  display: flex;\r\n  justify-content: flex-end;\r\n}\r\n.name_time_like {\r\n  line-height: 26px;\r\n}\r\n.time_like {\r\n  display: flex;\r\n  font-size: 12px;\r\n}\r\n.time_like .like {\r\n  padding: 0 5 0 0;\r\n}\r\n.time_like .time {\r\n  padding: 0 5;\r\n}\r\n.load {\r\n  text-align: center;\r\n}\r\n</style>\r\n<template>\r\n  <div id=\"comments\">\r\n    <div class=\"container\">\r\n      <div class=\"edit_place \">\r\n        <img :src=\"user.image_url\" alt=\"\" class=\"image_url\">\r\n        <textarea name=\"\" id=\"\" class=\"commonet_edit\" rows=\"5\" ref=\"comment\"></textarea>\r\n      </div>\r\n      <div style=\"padding:5px;\" class=\" btn_place\">\r\n        <button class=\"btn btn-success ok_btn\" @click=\"ok_btn\">发表</button>\r\n      </div>\r\n\r\n      <div class=\"show_commonets\">\r\n        <div class=\"title\">\r\n          <span>1条评论 </span>\r\n          <span class=\"right\">\r\n            <small @click=\"init('like_num','desc')\">按喜欢排序 </small>\r\n            <small @click=\"init('update_time','desc')\">按时间正序</small>\r\n            <small @click=\"init('update_time','asc')\">按时间倒序</small>\r\n          </span>\r\n        </div>\r\n\r\n        <div class=\"load\" v-show=\"loadding\">\r\n          <img src=\"/huoshu/public/static/layer/theme/default/loading-1.gif\" alt=\"\"> loading\r\n        </div>\r\n        <div class=\"commet\" v-for=\"comment_item in comments\" v-show=\"!loadding\">\r\n          <div class=\"commeter_info\">\r\n            <img :src=\"comment_item.user_image_url\" alt=\"\" class=\"image_url\" />\r\n            <div class=\"name_time_like\">\r\n              <div class=\"name\">{{comment_item.user_name}}</div>\r\n              <div class=\"time_like\">\r\n                <div class=\"like\">like:{{comment_item.like_num}}\r\n                  <span class=\"glyphicon glyphicon-thumbs-up\" @click=\"like_up(comment_item.id)\"></span>\r\n                  <span class=\"glyphicon glyphicon-thumbs-down\" @click=\"like_down(comment_item.id)\"></span>\r\n                </div>\r\n                <div class=\"time\">\r\n                  <span class=\"glyphicon glyphicon-time\"></span>\r\n                  {{comment_item.update_time}}</div>\r\n              </div>\r\n            </div>\r\n          </div>\r\n\r\n          <div class=\"content\">\r\n            {{comment_item.comment}}\r\n          </div>\r\n        </div>\r\n\r\n        <nav aria-label=\"Page navigation\" v-show=\"!loadding\">\r\n          <ul class=\"pagination\">\r\n            <li>\r\n              <a href=\"#\" aria-label=\"Previous\">\r\n                <span aria-hidden=\"true\">&laquo;</span>\r\n              </a>\r\n            </li>\r\n            <li v-for=\"item in countPage\" @click=\"page_click(item)\" :class=\"item==page?'active':'none'\">\r\n              <a href=\"#\">{{item}}</a>\r\n            </li>\r\n            <li>\r\n              <a href=\"#\" aria-label=\"Next\">\r\n                <span aria-hidden=\"true\">&raquo;</span>\r\n              </a>\r\n            </li>\r\n          </ul>\r\n        </nav>\r\n      </div>\r\n    </div>\r\n\r\n  </div>\r\n</template>\r\n\r\n\r\n<script>\r\nimport Toast from \"vue-easy-toast\";\r\nVue.use(Toast);\r\n\r\nexport default {\r\n  data() {\r\n    return {\r\n      user: $user,\r\n      page: 1,\r\n      comments: [],\r\n      loadding: false,\r\n      countPage: 0\r\n    };\r\n  },\r\n  created() {\r\n    this.init();\r\n  },\r\n  methods: {\r\n    init(order = \"like_num\", asc = \"desc\") {\r\n      var self = this;\r\n      var data = {\r\n        page: self.$data.page,\r\n        article_id: $article[\"id\"],\r\n        order,\r\n        asc\r\n      };\r\n      ajax({\r\n        type: \"post\",\r\n        url: \"/huoshu/public/index/comment/read\",\r\n        data,\r\n        before() {\r\n          self.loadding = true;\r\n        },\r\n        success(returnJson) {\r\n          self.loadding = false;\r\n          self.$data.comments = JSON.parse(returnJson)[\"data\"];\r\n          self.$data.countPage = JSON.parse(returnJson)[\"count\"];\r\n        }\r\n      });\r\n    },\r\n    ajax_click_down(comment_id) {\r\n      var self = this;\r\n      var data = {\r\n        user: JSON.stringify($user),\r\n        comment_id\r\n      };\r\n      ajax({\r\n        type: \"post\",\r\n        url: \"/huoshu/public/index/comment/like_down\",\r\n        data,\r\n        success(returnJsonStr) {\r\n          var returnJson = JSON.parse(returnJsonStr);\r\n          layer.msg(returnJson.message);\r\n          Vue.toast(returnJson.message);\r\n          if (returnJson.success) {\r\n            self.loadding = true;\r\n            self.init();\r\n          }\r\n        }\r\n      });\r\n    },\r\n    ajax_lick_up(comment_id) {\r\n      var self = this;\r\n      var data = {\r\n        user: JSON.stringify($user),\r\n        comment_id\r\n      };\r\n      ajax({\r\n        type: \"post\",\r\n        url: \"/huoshu/public/index/comment/like\",\r\n        data,\r\n        success(returnJsonStr) {\r\n          var returnJson = JSON.parse(returnJsonStr);\r\n          layer.msg(returnJson.message);\r\n          Vue.toast(returnJson.message);\r\n          if (returnJson.success) {\r\n            self.loadding = true;\r\n            self.init();\r\n          }\r\n        }\r\n      });\r\n    },\r\n    like_down(comment_id) {\r\n      if ($user.status == 0) {\r\n        Vue.toast(\"请先登录\", {\r\n          horizontalPosition: \"center\",\r\n          verticalPosition: \"bottom\"\r\n        });\r\n      } else {\r\n        this.ajax_click_down(comment_id);\r\n      }\r\n    },\r\n    like_up(comment_id) {\r\n      if ($user.status == 0) {\r\n        Vue.toast(\"请先登录\", {\r\n          horizontalPosition: \"center\",\r\n          verticalPosition: \"bottom\"\r\n        });\r\n      } else {\r\n        this.ajax_lick_up(comment_id);\r\n      }\r\n    },\r\n    page_click(page1) {\r\n      this.$data.page = page1;\r\n      this.init();\r\n    },\r\n    ok_btn() {\r\n      var html = this.$refs[\"comment\"].value;\r\n      var data = {\r\n        article: JSON.stringify($article),\r\n        user: JSON.stringify($user),\r\n        comment: html\r\n      };\r\n      var self = this;\r\n      ajax({\r\n        type: \"post\",\r\n        url: \"/huoshu/public/index/article/add_comment\",\r\n        data,\r\n        before() {\r\n          layer.load(1);\r\n        },\r\n        success(returnJson) {\r\n          layer.closeAll();\r\n          layer.msg(\"保存成功\");\r\n          self.init();\r\n        }\r\n      });\r\n    }\r\n  }\r\n};\r\n</script>\r\n"],"sourceRoot":""}]);

